@import url(./styles/home.css);
@import url(./styles/sidebar.css);
@import url(./styles/footer.css);

@font-face {
  font-family: 'Roboto';
  src: url('./fonts/Roboto-Regular.ttf') format('ttf');
  font-weight: 400;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
}

body {
  display: grid;
  grid-template: 9fr 1fr / 1fr 3.5fr;
  height: 100vh;
  width: 100vw;
}